import React, { useState } from "react";
import Card from "react-bootstrap/Card";
import { Container, Row, Col } from "react-bootstrap";
import R from "../Assests/R.png";
import P from "../Assests/P.png";
import AR from "../Assests/AR.png";
import D from "../Assests/D.png";
import A from "../Assests/A.png";
import B from "../Assests/B.png";
import M from "../Assests/M.png";
import Accordion from "react-bootstrap/Accordion";
import Footerlogo from "../Assests/Footerlogo.png";
import Instagram from "../Assests/Instagram.png";
import Snap from "../Assests/Snap.png";
import Button from "react-bootstrap/Button";
import Nav from "react-bootstrap/Nav";
import Navbar from "react-bootstrap/Navbar";
import Offcanvas from "react-bootstrap/Offcanvas";
import websiteLogo from "../Images/websiteLogo.png";
import i18next from "i18next";

import "../style/style.css";
import { withTranslation } from "react-i18next";

import { SliderContent } from "./SliderContent";
import Slider from "react-slick";
import "slick-carousel/slick/slick.css";
import "slick-carousel/slick/slick-theme.css";
import { TeachersSectionContent } from "./TeachersSectionContent";

function Home({ t }) {
  const settings = {
    dots: false,
    infinite: true,
    arrows: true,
    slide: "",
    rtl: false,

    speed: 1000,
    slidesToShow: 3,
    slidesToScroll: 3,
    autoplay: false,
    swipe: false,
    draggable: false,

    autoplaySpeed: 4000,
    rows: 1,
    responsive: [
      {
        breakpoint: 2000,
        settings: {
          slidesToShow: 3,
          slidesToScroll: 0,
        },
      },
      {
        breakpoint: 1024,
        settings: {
          slidesToShow: 3,
          slidesToScroll: 0,
        },
      },
      {
        breakpoint: 768,
        settings: {
          slidesToShow: 2,
          slidesToScroll: 0,
          arrows: false,
        },
      },
      {
        breakpoint: 480,
        settings: {
          slidesToShow: 1,
          slidesToScroll: 0,
          arrows: false,
        },
      },
      {
        breakpoint: 100,
        settings: {
          slidesToShow: 1,
          slidesToScroll: 0,
          arrows: false,
        },
      },
    ],
  };
  return (
    <div>
      <Container className="mt-4">
        <Row>
          <Col xs={6} sm={6} md={6} lg={4}>
            <img src={websiteLogo} width={130} alt="" />
          </Col>
          <Col xs={6} sm={6} md={6} lg={4} className="text-end text-lg-center">
            <button className="btn btn-danger ">Book A Seesion</button>
          </Col>
          <Col
            lg={4}
            className="d-lg-flex justify-content-end items-content-end d-none"
          >
            <div className="d-flex flex-direction-col justify-content-end items-content-center">
              <div>
                <span
                  style={{
                    color: "red",
                    marginRight: "10px",
                    cursor: "pointer",
                  }}
                >
                  Features
                </span>

                <span
                  style={{
                    color: "red",
                    marginRight: "10px",
                    cursor: "pointer",
                  }}
                >
                  Pricing
                </span>

                <span style={{ color: "red", cursor: "pointer" }}>Support</span>
              </div>
              <div className="d-flex justify-content-end items-content-end">
                <select
                  className="language_convert"
                  onChange={(e) => i18next.changeLanguage(e.target.value)}
                >
                  <option value="en">English</option>
                  <option value="ar">Arabic</option>
                </select>
              </div>
            </div>
          </Col>
        </Row>
      </Container>

      <Container className="mt-5">
        <Row>
          <Col>
            <h1 className="text-center responsive-font-size ">{t("Text4")}</h1>
            <div className="div6"></div>
          </Col>
        </Row>
      </Container>

      <Container className="mt-5">
        <Row>
          <Col sm={12} md={4} lg={4}>
            <Card className="Card programming_second_section">
              <Card.Img className="img1 " src={R} />
              <Card.Body style={{ margin: "0px", padding: "0px" }}>
                <Card.Title className="text-center t1">{t("Text5")}</Card.Title>
                <Card.Text>
                  <p
                    className="text-center"
                    style={{ margin: "0px", padding: "0px" }}
                  >
                    {t("StudentsExperience")}
                  </p>
                </Card.Text>
              </Card.Body>
            </Card>
          </Col>
          <Col sm={12} md={4} lg={4}>
            <Card className="Card programming_second_section">
              <Card.Img className=" img1" src={P} />
              <Card.Body>
                <Card.Title className="text-center t1">{t("Text6")}</Card.Title>
                <Card.Text>
                  <p className="text-center">{t("Teaching The Fundemental")}</p>
                </Card.Text>
              </Card.Body>
            </Card>
          </Col>
          <Col sm={12} md={4} lg={4}>
            <Card className="Card programming_second_section">
              <Card.Img className=" img1" src={AR} />
              <Card.Body>
                <Card.Title className="text-center">{t("Text7")}</Card.Title>
                <Card.Text>
                  <p className="text-center">{t("Understanding what")}</p>
                </Card.Text>
              </Card.Body>
            </Card>
          </Col>
        </Row>
      </Container>

      <Container fluid>
        <Row className="starsMission">
          <Col sm={12} md={12} lg={6} xl={6} className="">
            <h1 className="text-center mt-5 h1">{t("Stars Mission")}</h1>
            <p className="P">{t("Welcome to Young Stars")}</p>
          </Col>
          <Col sm={6} md={6} lg={6} xl={6} className="young_image_section">
            <img className="img2 mt-5" src={D} alt="" />
          </Col>
        </Row>
      </Container>

      <Container>
        <Row className="mt-5 technical_development">
          <Col sm={12} md={6} lg={6} className="technical_development_1">
            <img className="img3 mt-5 technical_development_1" src={A} alt="" />
          </Col>
          <Col sm={12} md={6} lg={6} className="technical_development_2">
            <h1 className="text-center Text">{t("TechnicalDevelopment")}</h1>
            <p className="P mt-4">{t("Focusing on fun learning")}</p>
          </Col>
        </Row>
      </Container>

      <Container>
        <Row className="mt-5 technical_development">
          <Col sm={12} md={6} lg={6} className="mental_development mt-3">
            <h1 className="text-left">{t("MentalDevelopment")}</h1>
            <p className="">{t("Modules and tasks")}</p>
          </Col>
          <Col sm={12} md={6} lg={6} className="technical_development_1">
            <img className="img3 mt-5 technical_development_1" src={B} alt="" />
          </Col>
        </Row>
      </Container>

      {/* Testimonials Slider */}

      {/* With Slider */}
      <Container>
        <Slider {...settings} className="text-center ">
          {SliderContent.map((data) => (
            <Row className="">
              <Col className="programming_slider">
                <div className="d-flex justify-content-center">
                  <img
                    src={data.image}
                    alt="Icon"
                    width={150}
                    className=""
                    style={{ borderRadius: "100%" }}
                  />
                </div>

                <div className="font-medium text-center text-lg pt-4 pb-10">
                  {t("Slate helps")}
                </div>
                <div className="mt-4">
                  <span class="fa fa-star checked"></span>
                  <span class="fa fa-star checked"></span>
                  <span class="fa fa-star checked"></span>
                  <span class="fa fa-star"></span>
                  <span class="fa fa-star"></span>
                </div>
                <h3 className="font-semibold text-2xl pt-4">{t(data.name)}</h3>
              </Col>
            </Row>
          ))}
        </Slider>
      </Container>

      {/* From Happy teachers and parents Section */}
      <Container style={{ marginTop: "70px" }}>
        <h3 className="text-center font-semibold" style={{ color: "#c13645" }}>
          {t("Happy teachers and parents")}
        </h3>
        <Slider {...settings} className="text-center ">
          {TeachersSectionContent.map((data) => (
            <Row className="">
              <Col sm={12} md={4} lg={4} className="mt-5 teacher_section">
                <div className="userName_professional_Section mt-5 pt-4 ">
                  <h4 className="text-center">{t(data.userName)}</h4>
                  <p className="text-center">{t(data.profession)}</p>
                </div>
              </Col>
            </Row>
          ))}
        </Slider>
      </Container>

      <Container className="Question">
        <Row>
          <Col>
            <h2 className="h3 text-center">{t("Asked Question")}</h2>
            <Accordion defaultActiveKey="0">
              <Accordion.Item className="accordion-item1" eventKey="1">
                <Accordion.Header>{t("Raw denim")}</Accordion.Header>
              </Accordion.Item>

              <Accordion.Item eventKey="2">
                <Accordion.Header>{t("Williamsburg")}</Accordion.Header>
              </Accordion.Item>

              <Accordion.Item eventKey="3">
                <Accordion.Header class="Raw">
                  <span style={{ color: "red" }}>{t("Raw denim")}</span>
                </Accordion.Header>
                <Accordion.Body className="body">
                  {t("Anim pariatur")}
                </Accordion.Body>
              </Accordion.Item>

              <Accordion.Item eventKey="4">
                <Accordion.Header>{t("Reprehenderit")}</Accordion.Header>
              </Accordion.Item>

              <Accordion.Item eventKey="5">
                <Accordion.Header>{t("Cosby")}</Accordion.Header>
              </Accordion.Item>
            </Accordion>
          </Col>
        </Row>
      </Container>

      <footer
        style={{
          background: "#b4b4b4",
          paddingTop: "40px",
          paddingBottom: "40px",
          marginTop: "150px",
        }}
      >
        <Container>
          <Row>
            <Col sm={12} md={12} lg={3}>
              <div>
                <div>
                  <img className="" src={Footerlogo} width={250} alt="" />
                </div>
                <div className="d-flex justify-content-around">
                  <img width={50} src={Instagram} alt="" />
                  <img width={50} src={Snap} alt="" />
                </div>
              </div>
            </Col>
            <Col
              sm={0}
              md={0}
              lg={9}
              className="d-lg-flex d-none justify-content-around items-content-"
            >
              <Col className="text-center" sm={12} md={12} lg={2}>
                <div style={{ cursor: "pointer" }}>Pages</div>
                <div style={{ cursor: "pointer" }}>Home</div>
                <div style={{ cursor: "pointer" }}>Product</div>
                <div style={{ cursor: "pointer" }}>Pricing</div>
                <div style={{ cursor: "pointer" }}>About</div>
                <div style={{ cursor: "pointer" }}>Contact</div>
              </Col>
              <div>
                <div style={{ cursor: "pointer" }}>Tomothy</div>
                <div style={{ cursor: "pointer" }}>Elenovr Edwards</div>
                <div style={{ cursor: "pointer" }}>Ted Roberston</div>
                <div style={{ cursor: "pointer" }}>Annette</div>
                <div style={{ cursor: "pointer" }}>Russel</div>
                <div style={{ cursor: "pointer" }}>Jinnie Mckinney</div>
                <div style={{ cursor: "pointer" }}>Golria Richard</div>
              </div>
              <div>
                <div style={{ cursor: "pointer" }}>Jane Black</div>
                <div style={{ cursor: "pointer" }}>Philip Jones</div>
                <div style={{ cursor: "pointer" }}>Product</div>
                <div style={{ cursor: "pointer" }}>Colleen Russell</div>
                <div style={{ cursor: "pointer" }}>Marvin Hawkins</div>
                <div style={{ cursor: "pointer" }}>Bruce Simmions</div>
              </div>
              <div>
                <div style={{ cursor: "pointer" }}>Annette</div>
                <div style={{ cursor: "pointer" }}>Jenniee Copper</div>
                <div style={{ cursor: "pointer" }}>Julie Henry</div>
                <div style={{ cursor: "pointer" }}>Johnny Murphy</div>
                <div style={{ cursor: "pointer" }}>Gregory Mccoy</div>
                <div style={{ cursor: "pointer" }}>Marvin Mckinney</div>
              </div>
            </Col>
          </Row>
        </Container>
      </footer>
    </div>
  );
}

export default withTranslation()(Home);
